.tabsRow {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-right: 20px;
}

.optionsDropdown {
  position: absolute;
}

.tabs::-webkit-scrollbar {
  height: 10px; /* Set the width of the scrollbar */
  background-color: var(--base-color); /* Set the background color of the scrollbar */
}

.tabs::-webkit-scrollbar-thumb {
  background-color: var(--base-secondary-color); /* Set the color of the scrollbar thumb */
  border-radius: 0; /* Set the border radius of the scrollbar thumb */
}

.tabs::-webkit-scrollbar-track {
  background-color: var(--base-color); /* Set the background color of the scrollbar track */
}
.Plus {
  height: 30px;
  width: 30px;
}
.tabs {
  display: flex;
  overflow-x: scroll;
  border-radius: 10px;
  margin: 0;
  background-color: #232323;
  scrollbar-width: thin;
  text-overflow: ellipsis;
}
.TabButton {
  position: relative;
  width: 70px;
  border-radius: 10px;
  text-overflow: ellipsis;
  background-color: transparent;
  color: white;
  border: none;
  opacity: 0.2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  gap: 10px;
}

.TabButton > p {
  text-overflow: ellipsis;
  text-wrap: nowrap;
  overflow: hidden;
}

#options {
  opacity: 0;
  display: none;
}

#optionsModal.modal {
  height: 300px;
  width: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
}

.TabButton.selected {
  opacity: 1;
  width: fit-content;
  background-color: #515151;
}

.TabButton.selected > #options {
  display: block;
  opacity: 1;
}

.modal h1 {
  margin: 0;
}

/* save button with rounded border, white text, and color of --accent-color */
.saveButton {
  border-radius: 4px;
  color: white;
  padding: 0.25em 4em;
  background-color: var(--accent-color);
  border: none;
  margin: 10px;
  font-size: 0.8em;
  font-weight: 100;
  cursor: pointer;
}

/* cancelButton with no background color, no border, and white text */
.cancelButton {
  border-radius: 4px;
  color: white;
  padding: 0.25em 1em;
  background-color: var(--base-color);
  border: none;
  margin: 10px;
  font-size: 0.8em;
  font-weight: 100;
  cursor: pointer;
}

.addView {
  display: flex;
  flex-grow: 1;
  flex-basis: content;
  flex-direction: column;
  align-content: stretch;
  align-items: stretch;
  justify-content: flex-start;
  gap: 10px;
  height: max-content;
  margin-right: 20px;
  padding-bottom: 10px;
  margin-bottom: 0;
}

.viewList {
  scrollbar-color: var(--base-secondary-color);
  scrollbar-width: 5px;
  padding: 5px;
  display: flex;
  flex-shrink: 1;
  flex-direction: column;
  align-content: stretch;
  align-items: stretch;
  justify-content: flex-start;
  gap: 10px;
  height: 100%;
  overflow-y: scroll;
  margin-right: 10px;
  padding: 0;

  padding-right: 5px;
}

.viewList::-webkit-scrollbar {
  width: 5px;
}

.viewList::-webkit-scrollbar-track {
  background-color: transparent;
}

.viewList::-webkit-scrollbar-thumb {
  background-color: var(--base-secondary-color);
  border-radius: 100px;
}

.buttons {
  display: flex;
  flex-grow: 1;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: flex-end;
  align-items: flex-end;
  margin-right: 5px;
}

.labels {
  display: grid;
  gap: 5px;
  grid-template-columns: 1fr 3fr;
  margin-right: 20px;
}

.labels > p {
  margin: 0;
  box-sizing: border-box;
  padding: 0px 5px;
  font-weight: 400;
  font-size: 0.8em;
}
